.calendar{position:absolute;width:315px;top:100%;z-index:5;background-color:#fff;box-shadow:0 14px 45px rgba(0,0,0,0.25),0 10px 18px rgba(0,0,0,0.22);-moz-box-shadow:0 14px 45px rgba(0,0,0,0.25),0 10px 18px rgba(0,0,0,0.22);-webkit-box-shadow:0 14px 45px rgba(0,0,0,0.25),0 10px 18px rgba(0,0,0,0.22)}.calendar_header{background-color:#0B610B;color:white;padding:13px 20px 20px 20px;height:100px}.calendar_year{position:relative;overflow:hidden;height:16px;opacity:0.7;margin-top:15px;margin-bottom:0px;line-height:16px}.calendar_date{position:relative;overflow:hidden;height:40px;font-size:18px;line-height:40px}.calendar_week{position:relative;font-size:12px;line-height:12px;color:rgba(0,0,0,0.8);padding:0 14px}.calendar_wd{float:left;width:41px;text-align:center}.calendar_days{position:relative;margin:14px 14px 0;height:205px;overflow:hidden;-webkit-transition:height 0.3s;transition:height 0.3s}.calendar_days.has-6-weeks{height:246px}.calendar_day{position:relative;width:41px;height:41px;float:left;text-align:center;line-height:41px;cursor:pointer;-webkit-transition:all 450ms cubic-bezier(0.23, 1, 0.32, 1) 0ms;transition:all 450ms cubic-bezier(0.23, 1, 0.32, 1) 0ms;-webkit-transition:all 450ms cubic-bezier(0.25, 0.2, 0.25);transition:all 450ms cubic-bezier(0.25, 0.2, 0.25)}.calendar_day:hover{color:white}.calendar_day:hover .calendar_day_effect{-webkit-transform:scale(1);transform:scale(1);opacity:0.4}.calendar_day.selected{color:white}.calendar_day.selected .calendar_day_effect{-webkit-transform:scale(1);transform:scale(1);opacity:1}.calendar_day_effect{position:absolute;top:3px;left:3px;width:36px;height:36px;border-radius:50%;background-color:#088A08;-webkit-transition:all 450ms cubic-bezier(0.23, 1, 0.32, 1) 0ms;transition:all 450ms cubic-bezier(0.23, 1, 0.32, 1) 0ms;-webkit-transform:scale(0);transform:scale(0);opacity:0}.calendar_day_text{position:relative}.calendar_controls{position:relative;height:56px;line-height:56px;text-align:center}.calendar_controls button{position:relative;border:none;background-color:transparent;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none;outline:none;cursor:pointer;width:56px;height:56px}.calendar_controls svg{width:24px;height:24px;fill:rgba(0,0,0,0.87);vertical-align:middle}.calendar_month{position:absolute;height:16px;line-height:16px;overflow:hidden;top:20px;left:0;right:0;bottom:0}.calendar_next{float:right}.calendar_prev{float:left}.calendar_actions{text-align:right;padding:8px}.calendar_actions button{border:none;background-color:transparent;display:inline-block;cursor:pointer;outline:none;color:#0B610B;font-size:14px;text-transparent:uppercase;font-weight:500;min-width:80px;line-height:36px;text-align:center;-webkit-appearence:none;-webkit-transition:all 0.3s;transition:all 0.3s}.calendar_actions button:hover{background-color:rgba(153,153,153,0.2)}.calendar-slide-transition{opacity:1;-webkit-transition:all 0.3s;transition:all 0.3s;-webkit-transform:translateY(0);transform:translateY(0)}.calendar-slide-leave,.calendar-slide-enter{opacity:0;-webkit-transform:translateY(-15px);transform:translateY(-15px)}.slidev-transition{position:absolute;top:0;right:0;left:0;bottom:0;transition:all 0.3s;-webkit-transition:all 0.3s;-moz-transition:all 0.3s;-webkit-transform:translateX(0);transform:translateX(0);opacity:1}.slidev-transition.off{-webkit-transition-duration:0s;transition-duration:0s}.slidev-leave,.direction-prev.slidev-enter{-webkit-transform:translateX(-100%);transform:translateX(-100%);opacity:0}.slidev-enter,.direction-prev.slidev-leave{-webkit-transform:translateX(100%);transform:translateX(100%);opacity:0}.slideh-transition{position:absolute;top:0;right:0;left:0;bottom:0;transition:all 0.3s;-webkit-transition:all 0.3s;-moz-transition:all 0.3s;-webkit-transform:translateY(0);transform:translateY(0);opacity:1}.slideh-transition.off{-webkit-transition-duration:0s;transition-duration:0s}.slideh-leave,.direction-prev.slideh-enter{-webkit-transform:translateY(-100%);transform:translateY(-100%);opacity:0}.slideh-enter,.direction-prev.slideh-leave{-webkit-transform:translateY(100%);transform:translateY(100%);opacity:0}



